
.content {
    padding-top: 40px;
}

.headerText{
    text-align: center;
    margin-top: 10px;
}

.buttons {
    text-align: center;
}

.buttons button{
    margin: 40px;
}

* {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  color: #333;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.6; }

.header {
  background: #20232a; }

.header__title {
  color: white;
  text-decoration: none;
  padding-left: 4.8rem; }
  .header__title h1 {
    margin: 0; }

.header--link {
  color: #f7f7f7;
  padding-right: 4.8rem; }

.login--form {
  width: 50%;
  margin: 0 auto;
  margin-top: 10%; }

.login--inner {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 1.2rem; }

.input--form {
  font-size: 100%;
  margin-bottom: 15px; }

.login--title {
  text-align: center;
  letter-spacing: 1px;
  margin-bottom: 10px; }

.form--alert {
  text-align: center;
  width: 50%;
  margin: 0 auto;
  letter-spacing: 1px; }

.user-button {
  margin-top: 20px; }

html, body {
  width: 100%;
  height: 100%;
  position: absolute; }

.side_section {
  background-color: #4e555b; }

.side_section {
  color: white; }

.submit_button {
  text-align: center; }

.side_section_date {
  font-size: 1.3em;
  padding-top: 10px; }

.side_section_user_data {
  padding-top: 30px; }

.side_section_stats {
  padding-top: 50px;
  padding-bottom: 50px; }

.side_section_stats h2 {
  font-size: 1.2em; }

.section_header {
  font-size: 2em;
  padding-bottom: 20px;
  padding-top: 40px; }

.award_table thead {
  font-size: .85em;
  font-weight: bold; }

.award_data th {
  font-weight: normal; }

.input_width {
  width: 100%; }

.award_creation_buttons {
  text-align: center; }

.admin--dash {
  padding-top: 3.2rem; }

.admin--menu {
  margin: 0;
  margin-top: 50px;
  width: 200px;
  height: 100%;
  overflow: auto; }

.admin--menu a {
  display: block;
  color: black;
  padding: 1.2rem;
  text-decoration: none;
  text-align: left;
  border: 1px solid black; }

.admin--menu a:hover {
  background-color: #555;
  color: white; }

.admin--menu h1 {
  text-align: left;
  padding: 0 1.2rem;
  text-decoration: underline; }

.admin--dash--content {
  margin-left: 1%;
  padding: 15px 2px;
  height: 1000px; }

.admin--title h1 {
  float: left; }

.admin--add {
  float: right; }

.report {
  margin: 3.2rem; }

